import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import firebase from 'firebase'

import { HomePage } from '../pages/home/home';
@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = HomePage;

  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
      const config = {
        apiKey: "AIzaSyA2AYf1_vZDZ-AoADMMc349p14yoXi6OJM",
        authDomain: "eltonbarbosa-de4cb.firebaseapp.com",
        databaseURL: "https://eltonbarbosa-de4cb.firebaseio.com",
        projectId: "eltonbarbosa-de4cb",
        storageBucket: "eltonbarbosa-de4cb.appspot.com",
        messagingSenderId: "715603000599"
      };
      firebase.initializeApp(config);
    });
  }
}




